
import Vue from 'vue'

export default Vue.extend({
  name: 'FilterDrawer',
  data: () => {
    return {
      display: false,
    }
  },
  props: {
    value: Boolean,
    clearButton: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    value(val) {
      this.display = val
    },
    display(val) {
      !val && this.$emit('input', false)
    },
  },
  methods: {
    clear() {
      this.$emit('clear')
    },
  },
})
