
import Vue from 'vue'

export default Vue.extend({
  name: 'PageSizeSelect',
  data: () => {
    return {
      selection: 0,
    }
  },
  props: {
    items: {
      type: Array,
      default: () => [20, 50, 100, 200, 500],
    },
    value: {
      type: Number,
      required: true,
    },
    dark: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    value: {
      handler(val) {
        this.selection = this.items.findIndex(x => x === val)
      },
      immediate: true,
    },
  },
  methods: {
    setPageSize (size: number) {
      this.$emit('input', size)
    },
  },
})
